.weather-header{
    width: 100%;
    background-color: rgba(0,0,0,0.3);
    display: flex;
    @media only screen and (max-width:500px){
        flex-wrap: wrap;
        flex-direction: column-reverse;
    }

    &__left{
        display: flex;
        flex-wrap: wrap;
        width: 50%;
        padding: 1rem;
        @media only screen and (max-width:500px){
            width: 100%;
        }
        .left-temp{
            
            width: 100%;
            color: #fff;
            font-size: 4rem;
            text-align: center;
            h2{
                display: inline-block;
                position: relative;
                box-sizing: content-box;
                margin: 0;
                padding:0;
                .bgc{
                    position: absolute;
                    top: 22px;
                    right: -35px;
                    width: 0.8rem;
                    height: 0.8rem;
                    border: 5px solid #fff;
                    border-radius: 50%;
                }
            }
            
        }
        .left-main{
            width: 100%;
            padding:1rem;
            font-size: 1.5rem;
            color: #fff;
            text-align: center; 
        }
        .left-humidity{
            box-sizing: border-box;
            padding:1rem;
            width: 50%;
            font-size: 1rem;
            color: #fff;
            text-align: center;
        }
        .left-wind{
            box-sizing: border-box;
            padding:1rem;
            width: 50%;
            border-left: 1px solid #fff;
            font-size: 1rem;
            color: #fff;
            text-align: center;
        }
    }
    &__right{
        width: 50%;
        padding-top: 1rem;
        color: #fff;
        text-align: center;
        @media only screen and (max-width:500px){
            width: 100%;
        }
        .right-title {
            position: relative;
            display: inline-block;
            &::before {
                position: absolute;
                left: 15px;
                bottom: -8px;
                content: '';
                box-sizing: border-box;
                width: 70px;
                height: 2px;
                border: 2px solid #fff;
            }
        }
    }  
}