.weather-main{
    width: 100%;
    display: flex;
    background-color: rgba(182, 139, 196, 0.8);
    @media only screen and(max-width:500px){
        flex-direction: column-reverse;
        text-align: center;
    }
    &__left{
        width: 30%;
        text-align: center;
        @media only screen and(max-width:500px){
            width: 100%;
        }
        .left-title{
            display: inline-block;
            margin-right: 10px;
        }
        .left-feedback {
            list-style-type: none;
            height: 200px;
            overflow-y: scroll;
            scrollbar-width: thin;
            overflow-x: hidden;
            li {
                display: flex;
                align-items: center;
                .photo { 
                    width: 50px;
                    height: 50px;
                    border-radius: 50%;
                    display: inline-block;
                    margin-right: 10px;
                }
                p { 
                    height: 55px;
                    margin: 10px 0;
                    display: inline-block;
                    overflow: hidden;
                }
            }
        }
    }
    &__right {
        width: 70%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        @media only screen and(max-width:500px){
            width: 100%;
            flex-direction: column;;
        }
        .dayInfo {
            width: 160px;
            text-align: center;
            margin-top: 2rem;
            display: flex;
            flex-wrap: wrap;
            @media only screen and(max-width:500px){
                margin: 20px auto;
            }
            &-day{
                width: 100%;
                
            }
            &-icon{
                width: 100%;
                height: 50px;
                img{
                    width: 50%;
                }
            }
            &-highTemp{
                width: 50%;
                font-weight: 700;
                font-size: 2rem;
            }
            &-lowTemp{
                width: 50%;

            }
        }
    }

}